"use client"
import React, { useEffect, useState } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Select, SelectTrigger, SelectContent, SelectItem, SelectValue } from "@/components/ui/select"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Checkbox } from "@/components/ui/checkbox"
import { toast } from "@/hooks/use-toast"
import axios from "@/utils/axiosConfig"
import { useStore } from "@/store"
import { Patient } from "@/models/user"

import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
} from "@/components/ui/command"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
import { ChevronsUpDown } from "lucide-react"

interface RegisterVaccinationModalProps {
  open: boolean
  onClose: () => void
}

export function RegisterVaccinationModal({ open, onClose }: RegisterVaccinationModalProps) {
  const { allUsers } = useStore((state) => state.profile)             // Ly danh sch parent
  const vaccines = useStore((state) => state.product.vaccines)

  const [selectedParentId, setSelectedParentId] = useState<number | "">("")
  const [selectedParent, setSelectedParent] = useState<Patient | null>(null)

  const [firstName, setFirstName] = useState("")
  const [lastName, setLastName] = useState("")
  const [email, setEmail] = useState("")
  const [mobileNo, setMobileNo] = useState("")

  const [vaccinationDate, setVaccinationDate] = useState<string>("")
  const [childProductMap, setChildProductMap] = useState<{ [childId: number]: number[] }>({})

  const [searchTerm, setSearchTerm] = useState("")

  const filteredUsers = allUsers?.filter(user => 
    user.fullname.toLowerCase().includes(searchTerm.toLowerCase()) || 
    user.id.toString().includes(searchTerm)
  ) || []

  // Khi user chn parent t dropdown
  const handleSelectParent = async (val: string) => {
    const parentId = Number(val)
    setSelectedParentId(parentId)
  }

  // Mi khi selectedParentId thay i, load chi tit user (nu cn) 
  // hoc ly trc tip t allUsers. 
  useEffect(() => {
    if (selectedParentId) {
      // Gi s ta tm ngay trong allUsers
      const p = allUsers.find((u: any) => u.id === selectedParentId)
      if (p) {
        setSelectedParent(p)

        // Tch firstName, lastName t p.fullname (tu logic)
        const [fname, ...rest] = p.fullname.split(" ")
        setFirstName(fname || "")
        setLastName(rest.join(" ") || "")
        setEmail(p.email || "")
        setMobileNo(p.phone || "")

        // Xy dng childProductMap rng cho tt c children
        const initMap: { [childId: number]: number[] } = {}
        p.children?.forEach((child: any) => {
          initMap[child.childId] = []
        })
        setChildProductMap(initMap)
      }
    } else {
      // reset
      setSelectedParent(null)
      setFirstName("")
      setLastName("")
      setEmail("")
      setMobileNo("")
      setChildProductMap({})
    }
  }, [selectedParentId, allUsers])

  // X l khi check/ uncheck vaccine cho mt a tr
  const handleVaccineCheck = (childId: number, vaccineId: number, checked: boolean) => {
    setChildProductMap((prev) => {
      const oldList = prev[childId] || []
      let newList = []
      if (checked) {
        // add vaccineId
        newList = [...oldList, vaccineId]
      } else {
        // remove vaccineId
        newList = oldList.filter((id) => id !== vaccineId)
      }
      return {
        ...prev,
        [childId]: newList,
      }
    })
  }

  // Submit
  const handleSubmit = async () => {
    // ParentId
    if (!selectedParentId) {
      toast({
        title: "Error",
        description: "Please select a parent first",
        variant: "destructive",
      })
      return
    }

    // To payload
    const payload = {
      parentId: selectedParentId, // Bt buc
      firstName,
      lastName,
      email,
      mobileNo,
      childProductMap,
      vaccinationdate: vaccinationDate, // "2025-03-29T15:24:19.506Z"
    }

    try {
      const token = localStorage.getItem("token")
      await axios.post("/order/create-by-product/", payload, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      toast({
        title: "Success",
        description: "Order created successfully",
      })
      onClose()
    } catch (err) {
      console.error(err)
      toast({
        title: "Error",
        description: "Failed to create order",
        variant: "destructive",
      })
    }
  }

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-[800px]">
        <DialogHeader>
          <DialogTitle>Register Vaccination for User</DialogTitle>
          <DialogDescription>
            Staff can help create an order for a selected user (parent).
          </DialogDescription>
        </DialogHeader>

        {/* Chn Parent */}
        <div className="my-4 space-y-2">
          <Label>Select Parent</Label>
          <Popover>
            <PopoverTrigger asChild>
              <Button
                variant="outline"
                role="combobox"
                className="w-full justify-between"
              >
                {selectedParentId
                  ? allUsers.find((user) => user.id === selectedParentId)?.fullname
                  : "Select parent..."}
                <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
              </Button>
            </PopoverTrigger>
            <PopoverContent className="w-full p-0">
              <Command>
                <CommandInput
                  placeholder="Search parent..."
                  value={searchTerm}
                  onValueChange={setSearchTerm}
                />
                <CommandEmpty>No parent found.</CommandEmpty>
                <CommandGroup className="max-h-[300px] overflow-y-auto">
                  {filteredUsers.map((user) => (
                    <CommandItem
                      key={user.id}
                      value={user.id.toString()}
                      onSelect={() => {
                        handleSelectParent(user.id.toString())
                      }}
                    >
                      {user.fullname} (ID: {user.id})
                    </CommandItem>
                  ))}
                </CommandGroup>
              </Command>
            </PopoverContent>
          </Popover>
        </div>

        {/* Guardian Info */}
        {selectedParent && (
          <div className="border rounded-md p-4 space-y-4 mb-4">
            <div className="flex gap-4">
              <div className="w-1/2 space-y-2">
                <Label>First Name</Label>
                <Input
                  value={firstName}
                  onChange={(e) => setFirstName(e.target.value)}
                />
              </div>
              <div className="w-1/2 space-y-2">
                <Label>Last Name</Label>
                <Input
                  value={lastName}
                  onChange={(e) => setLastName(e.target.value)}
                />
              </div>
            </div>

            <div className="flex gap-4">
              <div className="w-1/2 space-y-2">
                <Label>Email</Label>
                <Input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="w-1/2 space-y-2">
                <Label>Mobile No</Label>
                <Input
                  type="tel"
                  value={mobileNo}
                  onChange={(e) => setMobileNo(e.target.value)}
                />
              </div>
            </div>
          </div>
        )}

        {selectedParent && Array.isArray(selectedParent.children) && selectedParent.children.length > 0 && (
          <div className="space-y-4 mb-4">
            <h3 className="text-lg font-semibold">
              Select Vaccines for Children
            </h3>

            {selectedParent.children.map((child) => (
              <div key={child.childId} className="border p-3 rounded-md">
                <p className="font-medium mb-2">
                  Child: {child.fullname} (ID: {child.childId})
                </p>
                <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
                  {vaccines?.map((v) => {
                    const checked = childProductMap[child.childId]?.includes(v.id) || false
                    return (
                      <label
                        key={v.id}
                        className="flex items-center space-x-2 border p-2 rounded-md"
                      >
                        <Checkbox
                          checked={checked}
                          onCheckedChange={(checkedValue) => {
                            handleVaccineCheck(child.childId, v.id, !!checkedValue)
                          }}
                        />
                        <span>{v.title}</span>
                      </label>
                    )
                  })}
                </div>
              </div>
            ))}
          </div>
        )}

        {/* Ngy tim */}
        <div className="my-4 space-y-2">
          <Label>Vaccination Date</Label>
          <Input
            type="datetime-local"
            value={vaccinationDate}
            onChange={(e) => setVaccinationDate(e.target.value)}
          />
          <p className="text-sm text-gray-500">
            E.g. 2025-03-29T15:24
          </p>
        </div>

        {/* Action buttons */}
        <div className="mt-6 flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={handleSubmit}>Create Order</Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}
